    .line {
      height: 2px;
      width: 265px;
      border-top: 2px solid #fff;
      display: inline-block;
      position: relative; }


    .ani-start {
      animation: line-ani 3s linear;

      @keyframes line-ani {
        from {
          width: 0px; }
        to {
          width: 265px; } } }

    .line:after {
      position: absolute;
      right: 0px;
      top: -6px;
      content: '';
      display: inline-block;
      border: solid #fff;
      border-width: 0 2px 2px 0;
      padding: 4px;
      transform: rotate(-45deg);
      -webkit-transform: rotate(-45deg); }

    .line:before {
      position: absolute;
      left: 0px;
      top: -6px;
      content: '';
      display: inline-block;
      border: solid #fff;
      border-width: 0 2px 2px 0;
      padding: 4px;
      transform: rotate(-220deg);
      -webkit-transform: rotate(-220deg); }

    .line.slide-2:before {
      position: absolute;
      right: 0px;
      left: auto;
      top: -6px;
      transform: rotate(-45deg);
      -webkit-transform: rotate(-45deg); }


    .line-reverse {
      width: 227px;
      border-top: 2px dotted #fff;
      top: -17px;
      animation: line-ani-reverse 3s linear; }

    .slide-2 {
      width: 265px;
      position: relative;
      top: 35px;
      display: inline-block; }

    .slide-2:after {
      right: auto;
      left: 0px;
      transform: rotate(145deg);
      -webkit-transform: rotate(145deg); }

    .ani-none {
      animation-name: none!important; }

    .line-reverse:after {
      right: auto;
      left: 0px;
      transform: rotate(145deg);
      -webkit-transform: rotate(145deg);

      @keyframes line-ani-reverse {
        from {
          width: 0px; }
        to {
          width: 265px; } } }

    .fill-line {
      border-top: 2px solid #fff; }

    .doted-line {
      border-top: 2px dotted #fff;


      @keyframes line-ani-115 {
        from {
          width: 0px; }
        to {
          width: 127px; } } }

