/*line-timer start*/
.line-timer {
  width: 131px;
  height: 139px;
  display: inline-block;
  position: relative;
  padding-top: 5px;

  .count font {
    position: absolute;
    top: 50%;
    left: 40%;
    transform: translate(-50%, -50%);
    z-index: 10;
    font-size: 15px;
    color: #fff;
    z-index: 1000;
    line-height: 37px;
    height: 34px; }

  .count:after {
    content: counter(count);
    animation: counter 30s linear infinite;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
    font-size: 18px;
    font-weight: 500;
    color: #fff;
    background: radial-gradient(50% 50% at 50% 50%, #565555 0%, #212020 100%);
    width: 43px;
    height: 43px;
    border-radius: 43px;
    line-height: 43px;
    text-align: center; }

  @keyframes counter {
    0% {
      counter-increment: count 0; }
    1% {
      counter-increment: count 1; }
    5% {
      counter-increment: count 2; }
    10% {
      counter-increment: count 3; }
    15% {
      counter-increment: count 4; }
    20% {
      counter-increment: count 5; }
    25% {
      counter-increment: count 6; }
    30% {
      counter-increment: count 7; }
    35% {
      counter-increment: count 8; }
    40% {
      counter-increment: count 9; }
    45% {
      counter-increment: count 10; }
    48% {
      counter-increment: count 11; }
    50% {
      counter-increment: count 12; }
    100% {
      counter-increment: count 12; } }

  .count-reverse:after {
    animation: count-reverse 30s linear infinite;
    animation-delay: 15s; }

  @keyframes count-reverse {
    100% {
      counter-increment: count 0; }
    51% {
      counter-increment: count 0; }
    50% {
      counter-increment: count 1; }
    45% {
      counter-increment: count 2; }
    40% {
      counter-increment: count 3; }
    35% {
      counter-increment: count 4; }
    30% {
      counter-increment: count 5; }
    25% {
      counter-increment: count 6; }
    20% {
      counter-increment: count 7; }
    15% {
      counter-increment: count 8; }
    10% {
      counter-increment: count 9; }
    7% {
      counter-increment: count 10; }
    3% {
      counter-increment: count 11; }
    0% {
      counter-increment: count 12; } }

  .timer {
    width: 120px;
    height: 120px;
    position: relative;
    background: linear-gradient(180deg, #202020 0%, #1D1D1D 100%);
    margin: auto; }

  .revers-timer .timer {
    background: linear-gradient(180deg, #1B1B1B 0%, #191919 100%); }

  .timer:after {
    content: '';
    background: #404040;
    width: 1px;
    height: 99%;
    position: absolute;
    top: 50%;
    left: 49%;
    z-index: 1;
    transform: translate(-50%, -50%); }

  .timer, .timer * {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box; }

  .timer .pie {
    width: 50%;
    height: 100%;
    transform-origin: 100% 50%;
    position: absolute;
    background: transparent;
    border: 1px solid #fff; }

  .timer .pie-rev {
    transform-origin: 0% 50%;
    right: 0%; }

  .timer .spinner:before {
    content: '';
    position: absolute;
    display: block;
    z-index: 100;
    left: 55px;
    top: -8px;
    width: 0;
    height: 0;
    border-top: 8px solid transparent;
    border-left: 10px solid #fff;
    border-bottom: 8px solid transparent; }

  .timer .spinner-rev:before {
    content: '';
    position: absolute;
    display: block;
    z-index: 100;
    left: 0px;
    top: -8px;
    width: 0;
    height: 0;
    border-top: 8px solid transparent;
    border-left: 10px solid #fff;
    border-bottom: 8px solid transparent;
    transform: rotate(180deg); }

  .timer .spinner {
    border-radius: 100% 0 0 100% / 50% 0 0 50%;
    z-index: 0;
    border-right: none;
    -webkit-animation: rota 30s linear infinite; }

  .timer .spinner-rev {
    border-radius: 0% 100% 100% 0% / 0% 50% 50% 0%;
    z-index: 0;
    border-left: none;
    -webkit-animation: rota-rev 30s linear infinite;
    animation-delay: 15s; }

  .timer .filler {
    border-radius: 0 100% 100% 0 / 0 50% 50% 0;
    left: 50%;
    opacity: 0;
    -webkit-animation: opa-filler 30s steps(7, end) infinite;
    border-left: none; }

  .timer .filler-rev {
    border-radius: 100% 0 0 100% / 50% 0 0 50%;
    left: 0%;
    opacity: 1;
    -webkit-animation: opa-filler 30s steps(7, end) infinite;
    animation-delay: 15s;
    border-right: none; }

  .timer .mask {
    width: 52%;
    height: 100%;
    position: absolute;
    background: inherit;
    opacity: 1;
    left: -1px;
    -webkit-animation: opa 30s steps(7, end) infinite; }

  .timer .mask-rev {
    opacity: 0;
    left: 60px;
    -webkit-animation: opa 30s steps(7, end) infinite;
    animation-delay: 15s; }

  @-webkit-keyframes rota {
    0% {
      transform: rotate(0deg); }
    50% {
      transform: rotate(360deg);
      z-index: 0; }
    100% {
      transform: rotate(360deg);
      z-index: 100; } }

  @-webkit-keyframes rota-rev {
    100% {
      transform: rotate(0deg); }
    50% {
      transform: rotate(0deg);
      z-index: 0; }
    0% {
      transform: rotate(360deg);
      z-index: 0; } }

  @-webkit-keyframes opa {
    0%, 10%, 20%, 23% {
      opacity: 1; }
    27%, 75%, 100% {
      opacity: 0; } }

  @-webkit-keyframes opa-filler {
    0%, 10%, 25% {
      opacity: 0; }
    26%, 40%, 70%, 100% {
      opacity: 1; } }

  .timer-sml-text {
    position: absolute;
    top: 50%;
    left: -30px;
    font-size: 10px;
    transform: translate(-50%, -50%); } }

.line-timer-reverse {
  .timer-sml-text {
    right: -60px;
    left: auto; } }



/*line-timer end*/

