#section-staking-program {
  font-family: $font-family-heading;
  color: #fff;
  font-size: 12px;
  padding: 100px 0 70px 0;

  .main-section {
    text-align: center;
    padding: 0 15px;
    margin-bottom: 150px; }

  .main-title {
    font-size: 46px;
    color: #171717;
    padding-bottom: 0; }

  .inner-title {
    font-size: 36px;
    color: #fff;
    font-weight: 700;
    max-width: 930px;
    line-height: 54px;
    padding-bottom: 20px; }
  .staking-desc {
    p {
     margin-bottom: 0;
     font-size: 22px;
     font-weight: 700; } } }

.maha-coin-box {
  display: flex;
  justify-content: center;
  padding-top: 30px;

  .maha-coin img.coin-img {
    width: 165px; }

  .arrow {
    padding: 0 30px 0 30px;
    position: relative;
    top: 129px;

    img {
      position: relative;
      top: -56px; } }

  .maha-coin p {
    line-height: 26px;
    font-weight: 700;
    font-size: 22px;
    letter-spacing: 4px;
    letter-spacing: 7px;
    padding-top: 15px; } }

@media (max-width: 600px) {
  #section-staking-program {
    padding: 20px 0;

    .staking-desc p {
      font-size: 20px; }

    .maha-coin-box .arrow {
      padding: 0 10px;
      top: 101px;
      left: 5px; }

    .maha-coin img {
      width: 105px; }

    .maha-coin.arrow img {
      width: 40px; }

    .maha-coin p {
      font-size: 12px;
      line-height: 15px; }

    .maha-coin.ferrum-network p {
      top: 14px; } } }


